[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "cosa-nostra-icons";
  font-style: normal;
  font-weight: normal;
  speak: none;
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  text-align: center;
  /* opacity: .8; */
