/**
 * General
 */
body {
  font-family: 'Montserrat', sans-serif;
}
*,
::before,
::after {
  box-sizing: border-box;
}
/**
 * Hero/Intro
 */
/**
 * Hero/Intro for the page
 * Mobile then Desktop
 */
.hero {
  background: #3a2a2a url('images/hero.png') no-repeat center bottom;
  background-size: 105%;
  height: 100vh;
  position: relative;
  z-index: 1;
}
.hero .logo {
  position: absolute;
  top: 55px;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow: hidden;
  text-indent: -999px;
  background-image: url('images/logo.svg');
  background-repeat: no-repeat;
  background-position: 52.5% 43.5%;
  background-size: 290px;
  width: 127px;
  height: 40px;
}
@media screen and (orientation: landscape) {
  .hero .logo {
    top: 30px;
  }
}
.hero .lead {
  font-weight: 700;
  color: #d57a46;
  text-align: center;
  text-transform: uppercase;
  font-size: 28px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
  width: 100%;
}
@media screen and (orientation: landscape) {
  .hero .lead {
    transform: translate(-50%, -75%);
  }
}
.hero .animation-wrapper {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: auto;
  left: 50%;
  bottom: 0;
  overflow: hidden;
  transform: translateX(-50%);
}
@media screen and (orientation: landscape) {
  .hero .animation-wrapper {
    width: 40%;
  }
}
.hero .animation-wrapper img {
  position: absolute;
  animation-name: heroIntro;
  animation-duration: 1s;
  animation-timing-function: ease-out;
  animation-iteration-count: 1;
  animation-fill-mode: both;
  z-index: 1;
}
.hero .animation-wrapper .phone {
  display: block;
  width: 50%;
  height: auto;
  margin: 0 auto;
  position: relative;
  margin-bottom: -72px;
  animation-delay: 250ms;
  z-index: 2;
}
.hero .animation-wrapper .ss1 {
  top: 60px;
  left: 58%;
  width: 40%;
  animation-delay: 650ms;
}
.hero .animation-wrapper .ss2 {
  top: 12px;
  left: 50%;
  margin-left: -47%;
  width: 40%;
  animation-delay: 450ms;
}
@keyframes heroIntro {
  0% {
    transform: translateY(50%);
    opacity: 0;
  }
  100% {
    transform: translateY(0%);
    opacity: 1;
  }
}
/**
 * Description
 */
/**
 * Description block
 * With download link
 * Mobile then Desktop
 */
.description {
  padding: 46px 32px;
}
.description .download-btn {
  display: block;
  width: 172px;
  height: auto;
  margin: 0 auto 77px;
}
.description .download-btn img {
  display: block;
  width: 100%;
}
.description .title {
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 24px;
}
.description .title small {
  font-size: 12px;
  font-weight: 400;
  color: #c17144;
  display: block;
  margin-bottom: 10px;
}
.description .text-content {
  font-size: 12px;
  color: #929292;
  line-height: 2;
}
.description .text-content:first-of-type {
  margin-bottom: 22px;
}
/**
 * Video
 */
/**
 * Video block
 * Pre-configured iframe & video tags
 * Mobile then Desktop
 */
.video {
  position: relative;
  background: url('images/video.jpg') no-repeat center center / cover;
  padding: 118px 32px 102px;
  z-index: 1;
}
.video::before {
  content: '';
  background: url('images/video-pattern.png') repeat rgba(62, 44, 43, 0.77);
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}
.video .title {
  font-size: 16px;
  text-align: center;
  color: #fff;
  margin-bottom: 20px;
  font-weight: 400;
  line-height: 1.2;
}
.video .text-content {
  font-size: 22px;
  color: #ffa572;
  margin-bottom: 54px;
  text-align: center;
  font-weight: 700;
  line-height: 1.2;
}
.video .button {
  width: 220px;
  border-radius: 10px;
  display: block;
  margin: 0 auto;
  text-transform: uppercase;
  font-size: 14px;
  color: #3a2a2a;
  background: #d57a46;
  text-align: center;
  text-decoration: none;
  padding: 16px 0;
}
.video .button .icon-play-outline {
  font-size: 28px;
  display: inline-block;
  vertical-align: middle;
  margin-top: -4px;
}
.video .embed {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -2;
}
/**
 * Features
 */
/**
 * Game features
 * Mobile then Desktop
 */
.features {
  padding: 50px 80px;
}
.features .title {
  text-align: center;
  font-size: 12px;
  color: #d57a46;
  font-weight: 400;
  margin-bottom: 28px;
}
.features .title strong {
  font-size: 36px;
  display: block;
  color: #000;
  font-weight: 700;
}
.features .title::before,
.features .title::after {
  content: '\2666';
  color: #d3d3d3;
  display: block;
  margin-right: auto;
  margin-left: auto;
}
.features .title::before {
  margin-bottom: 8px;
}
.features .title::after {
  margin-top: 8px;
}
.features .feat-list li {
  margin: 26px auto;
  width: 160px;
  text-align: center;
}
.features .feat-list img {
  display: block;
  width: 75%;
  margin: 0 auto 16px;
}
.features .feat-list h4 {
  font-size: 16px;
  font-weight: 700;
  color: #d57a46;
  margin-bottom: 14px;
}
.features .feat-list p {
  font-size: 12px;
  color: #929292;
  line-height: 1.2;
}
/**
 * Download
 */
/**
 * Download
 * Mobile then Desktop
 */
.download {
  background: url('images/map.jpg') no-repeat center / cover;
  position: relative;
  z-index: 1;
  overflow: hidden;
  padding: 34px 32px 256px;
}
.download .title {
  font-size: 30px;
  font-weight: 400;
  color: #ffa572;
  text-transform: uppercase;
  margin-bottom: 14px;
  line-height: 1.2;
}
.download .title strong {
  font-weight: 700;
  display: block;
}
.download .text-content {
  font-size: 12px;
  color: #fff;
  margin-bottom: 40px;
  line-height: 1.2;
}
.download .download-btn {
  display: block;
  margin: 0 auto;
  width: 171px;
}
.download .download-btn img {
  display: block;
  width: 100%;
}
.download .gangsters {
  position: absolute;
  bottom: -300px;
  z-index: -1;
  left: 50%;
  margin-left: -133.5px;
  width: 277px;
}
/**
 * Footer
 */
/**
 * Footer
 * Mobile then Desktop
 */
.footer {
  background-color: #3a2a2a;
  padding: 44px 0 32px;
}
.footer .feedback {
  font-size: 12px;
  color: #745757;
  text-align: center;
  margin-bottom: 52px;
}
.footer .feedback a {
  color: inherit;
}
.footer .stayzest {
  display: block;
  width: 137px;
  height: 32px;
  text-indent: -999px;
  overflow: hidden;
  background: url('images/stayzest.png') no-repeat center / contain;
  margin: 0 auto 32px;
}
.footer .facebook {
  font-size: 24px;
  color: #c17144;
  border: 3px solid #c17144;
  text-align: center;
  width: 37px;
  height: 37px;
  line-height: 34px;
  text-decoration: none;
  border-radius: 50%;
  display: block;
  margin: 0 auto;
}
